@font-face{
    font-family: electrolize;
    src: url("/electrolize.woff2"), format("woff2");
}

body{
    font-family: electrolize !important;
}

.app-menu{
    width: 375px;
    margin: auto;
}

::-moz-selection{
    background-color: black;
    color: white;
}

::selection{
    background-color: black;
    color: white;
}
.canvas-container{
    text-align: center;
}

.canvas-container canvas{
    position: fixed;
    left: 0px;
    width: 100%;
}

.hud-chat{
    position: fixed;
    left: 10px;
    bottom: 10px;
}

.hud-chat input, textarea{
    background: -webkit-linear-gradient(top, white, silver);
    background: linear-gradient(180deg, white, silver);
    opacity: 0.9;
    border: 1px solid black;
    border-radius: 6px;
    padding: 4px;
    width: 300px;
    box-shadow: 1px 1px 1px;
}

.hud-chat input:focus, textarea:focus{
    outline: none;
}

.hud-chat textarea{
    height: 250px;
}

.hud-target, .hud-player{
    position: fixed; 
    background: -webkit-linear-gradient(top, white, silver); 
    background: linear-gradient(180deg, white, silver);
    opacity: 0.9;
    border: 1px solid black;
    border-radius: 6px;
    box-shadow: 1px 1px 1px;
    padding: 6px;
    width: 200px;
}

.hud-target{
    right: 225px;
    bottom: 175px;
}

.hud-player{
    right: 10px;
    bottom: 175px;
}

.hud-target table, .hud-player table{
    margin: auto;
}

.hud-abilities{
    opacity: 0.9;
    background: -webkit-linear-gradient(top, white, silver);
    background: linear-gradient(180deg, white, silver);
    position: fixed; 
    right: 10px;
    bottom: 10px;
    border: 1px solid black;
    border-radius: 6px;
    box-shadow: 1px 1px 1px;
    padding: 6px;
    width: 415px;
    text-align: center;
}

.hud-abilities img{
    border-radius: 25%;
    box-shadow: 1px 1px 1px;
    margin: 3px;
    cursor: pointer;
}

.hud-abilities img:disabled{
    cursor: not-allowed
}

.hud-dropdown{
    position: fixed;
    top: 10px;
    right: 10px;
}
